/* ==========================================================================
   Site styles
   ========================================================================== */

body, html {
	height: 100%;
	font-size: 14px !important;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

img { max-width: 100%; }

.slds-listbox__option:focus,
.group1:focus,
.group2:focus,
.group3:focus {
  -webkit-box-shadow: 0 0 5px rgba(81, 203, 238, 1);
          box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  padding: 3px 0px 3px 3px;
  margin: 5px 1px 3px 0px;
  border: 1px solid rgba(81, 203, 238, 1);
}

.mh-dropdown {
	max-height: calc(((.8125rem * 1.5) + 1rem) * 10);
	overflow-y: scroll;
}

/*  TYPOGRAPHY  ============================================================================= */
.caps { text-transform: uppercase; }
.italic { font-style:italic; }

.fw-light { font-weight: 200; }
.fw-norm { font-weight: 400; }
.fw-bold { font-weight: 800; }

.tac { text-align: center; }
.tal { text-align: left; }
.tar { text-align: right; }

.text-plain { text-decoration: none; }
.list-plain { padding: 0; margin: 0; list-style-type: none; }
.list-horz li { display: inline-block; }

/*  LAYOUT & DISPLAY  ============================================================================= */
.dn { display: none; }
.db { display: block; }
.dib { display: inline-block; }
.dt { display: table; }
.dtc { display: table-cell; }

.df { display: -ms-flexbox; display: flex; }
.df-center { -ms-flex-align: center; align-items: center; }
.df-stretch { -ms-flex-align: center; align-items: center; }
.df-justify { -ms-flex-pack: justify; justify-content: space-between; }
.df-around { -ms-flex-pack: distribute; justify-content: space-around; }
.df-start { -ms-flex-pack: start; justify-content: flex-start; }

.pos-rel { position: relative; }
.pos-abs { position: absolute; }
.pos-fix { position: fixed; }
.top-0 { top: 0; }
.bot-0 { bottom: 0; }
.left-0 { left: 0; }
.right-0 { right: 0; }

.fl { float: left; }
.fr { float: right; }

.center { margin-left: auto; margin-right: auto; }
.a-top { vertical-align: top; }
.a-mid { vertical-align: middle; }
.a-bot { vertical-align: bottom; }
.wi-full { width: 100%; }
.ht-full { height: 100%; }

.z-above { z-index: 10; }
.z-top { z-index: 999; }

/* Custom Display */
.mw-readability { max-width: 34rem; }
.mw-projects { max-width: 1200px; }

/* SHAPES & FORM DEFAULTS  ============================================================================= */
.circle { border-radius: 100%; }
.br-s { border-radius: 6px; }

button,
input[type="submit"] { border: none; background-color: transparent; }

input,
textarea { border: none; }

/* BORDER WIDTHS & STYLE ============================================================================= */
.ban { border: none !important; }
.bas { border-width: 1px; border-style: solid; }
.bls { border-left-width: 1px; border-left-style: solid; }
.brs { border-right-width: 1px; border-right-style: solid; }
.bts { border-top-width: 1px; border-top-style: solid; }
.bbs { border-bottom-width: 1px; border-bottom-style: solid; }
.border-gray { border-color: #dddbda; }

/*
 * COLORS
 */
.bg-navy { background-color: #001f3f; }
.text-lt-gray { color: #808080; }
.text-dr-gray { color: #666666; }
.text-white, .text-white a {
	color: #ffffff;
}
.text-aqua,
.text-aqua a {
	color: #7FDBFF;
}


/* ==========================================================================
TYPE
- Copy
- Type Scale
========================================================================== */

h1 {
  line-height: 1.3;
  font-weight: 300;
}

h2, h3, h4 {
  line-height: 1.3;
  font-weight: 400;
}


/* Copy ========================================================================== */

.intro    {  font-size: 32px; }
blockquote{  font-size: 32px; }



/* Type Scale
========================================================================== */

h1,.f1 {      font-size: 2.5rem; }
h2,.f2 {      font-size: 2rem; }
h3,.f3 {      font-size: 1.5rem; }
h4,.f4 {      font-size: 1rem; }
h5,.f5 {      font-size: 0.8rem; }
h6,.f6 {      font-size: 0.5rem; }

.meta {       font-size: 0.5rem; }


